import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  
  strict: true,
  state: {
    user: '',
    token: ''
  },
  mutations: {
    setToken(state, token) {
      state.token = token
    },
    setUser(state, user) {
      state.user = user
    },
  },
  actions: {
    login({commit}, data) {
      commit('setToken', data.token)
      commit('setUser', data.user)
    },
    logout({ commit }) {
      commit('setToken', '')
      commit('setUser', '')
    }
  }
})
